export default {
	data() {
	    return {
			existsCashCode:false,
			shopObj:{},
			currentSize:0
		}
	},
	methods: {
		url(e){
			uni.navigateTo({
				url:e.currentTarget.dataset.url
			})
		},
		clear(){
			uni.clearStorageSync();
			uni.showToast({
				title:"清理成功,请重新登录",
				icon:"none"
			})
			setTimeout(function() {
				uni.reLaunch({
					url: '/pages/login/login'
				});
			}, 2000);
		},
		exit(){
			uni.reLaunch({
				url: '/pages/login/login'
			});
		}
	},
	onLoad(){
		let that=this;
		uni.getStorageInfo({
			success: function (res) {
				that.currentSize=res.currentSize
			}
		});
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
	},
	onShow(){
		let that=this;
		that.$ajax({
			url:"/system_manager/existsCashCode",
			data:{
				merchantId:that.shopObj.merchantId
			},success(d){
				that.existsCashCode=d
			}
		})
	}
}
